export default {
	data() {
		return {
			isShowModel:false,
			goods: null,
			goodsList:[],
			//退货数量
			returnNum:1,
			//退款价格
			returnMoney: 0,
			//上传的图片列表
			images: [],
			reasons: [],
			currentReason: 0,
			selectReason: '',
			reasonTxt:'',
			typeTxt:'',
		}
	},
	onLoad() {
		this.getReason()
		this.typeTxt =uni.getStorageSync('typeTxt')
		this.selectReason='请选择'+this.typeTxt+'原因'
		this.goods = uni.getStorageSync('returnGoods')
		this.goods.maxNum = this.goods.goods_num
		let returnMoney = 0
		this.returnMoney = (this.returnNum * this.goods.final_price).toFixed(2)
		console.log(this.goods)
		this.goodsList = [this.goods]
	},
	methods: {
		getReason(){
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/Order/returnReason",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id')
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					this.reasons = res.data.data
				}
			})
		},
		//减少商品数量
		reduce(index) {
			if (this.returnNum == 1) {
				return
			}
			this.returnNum--
			this.returnMoney = (this.returnNum * this.goods.final_price).toFixed(2)
		},
		//添加商品数量
		add(index) {
			if(this.returnNum==this.goods.maxNum){
				this.showToast(this.typeTxt+'商品的数量不能超过购买的数量')
				return
			}
			this.returnNum++
			this.returnMoney = (this.returnNum * this.goods.final_price).toFixed(2)
		},
		showModel() {
			this.currentReason = -1
			this.isShowModel = true
		},
		closeModel() {
			this.isShowModel = false
		},
		//选择退货原因
		chooseReason(index) {
			this.currentReason = index
		},
		//点击确认原因
		sureReason() {
			this.selectReason = this.reasons[this.currentReason]
			this.closeModel()
		},
		//拍照
		takePhoto() {
			uni.chooseImage({
				count: 1, //默认9
				success: (res) => {
					console.log(res.tempFilePaths);
					this.images = res.tempFilePaths
				}
			});
		},
		//删除上传的照片
		delImage(index) {
			this.images.splice(index, 1)
		},
		toReturnDetail() {
			if(this.selectReason == '请选择'+this.typeTxt+'原因'){
				this.showToast('请选择'+this.typeTxt+'原因')
				return
			}
			//有上传凭证和没有上传凭证
			if(this.images.length==0){
				this.setheader()
				uni.request({
					url: this.websiteUrl + "/api/order/returnGoods",
					data: {
						token: uni.getStorageSync('token'),
						order_id:uni.getStorageSync('orderDetail').order_id,
						order_goods_id:uni.getStorageSync('returnGoods').rec_id,
						goods_num:this.returnNum,
						type:this.typeTxt=='退货'?1:2,
						reason:this.selectReason,
						describe:this.reasonTxt
					},
					method: "POST",
					header: this.header,
					success: (res) => {
						console.log(res.data)
						let result = res.data
						if(result.status==0){
							uni.showToast({
								title:'已提交申请',
								icon:'success',
								success: () => {
									setTimeout(function() {
										uni.navigateBack({
											delta:2
										})
									}, 1000);
								}
							})
							return
						}
						this.showToast(result.msg)
					}
				})
			}else{
				// console.log(uni.getStorageSync('returnGoods').rec_id)
				this.setheader()
				uni.uploadFile({
					url: this.websiteUrl + "/api/order/returnGoods",
					name: 'return_img',
					filePath: this.images[0],
					formData: {
						token: uni.getStorageSync('token'),
						shop_id: uni.getStorageSync('shop_id'),
						order_id:uni.getStorageSync('orderDetail').order_id,
						order_goods_id:uni.getStorageSync('returnGoods').rec_id,
						goods_num:this.returnNum,
						type:this.typeTxt=='退货'?1:2,
						reason:this.selectReason,
						describe:this.reasonTxt
					},
					method: "POST",
					header: this.header,
					success: (res) => {
						console.log(JSON.parse(res.data))
						let result = JSON.parse(res.data)
						if(result.status==0){
							uni.showToast({
								title:'已提交申请',
								icon:'success',
								success: () => {
									setTimeout(function() {
										uni.navigateBack({
											delta:1
										})
									}, 1000);
								}
							})
							return
						}
						this.showToast(result.msg)
					}
				})
			}
		},
	}
}
